.login {
  position: relative;
  background: transparent linear-gradient(180deg, #6BBB71 0%, #017961 100%) 0% 0% no-repeat padding-box;
  min-height: 100vh;
  padding-bottom: 154px;
  &__bot {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    background: transparent;
    padding: 68px 0; }
  &__close {
    position: absolute;
    z-index: 30;
    top: 28px;
    right: 36px;
    display: block;
    cursor: pointer;
    img {
      display: block;
      width: 16px;
      height: 16px; } }
  &__wrap {
    padding: 100px 0; }
  &__box {
    position: relative;
    .login__slide {
      background: #FFFFFF;
      padding: 50px 92px;
      border-radius: 5px;
      display: flex;
      position: relative; } }
  &__lside,
  &__rside {
    display: block;
    position: relative;
    width: 100%;
    max-width: 50%;
    flex: 0 0 50%;
    p {
      margin-bottom: 20px; } }
  &__lside {
    padding-right: 100px; }
  &__rside {
    padding-left: 100px;
    .login__logo {
      display: none; }
    &:before {
      content: "";
      display: block;
      position: absolute;
      width: 1px;
      height: calc(100% - 32px);
      top: 16px;
      left: 0;
      background: #AFAFAF; } }

  &__logo {
    display: block;
    position: relative;
    img {
      display: block;
      height: 34px;
      width: auto; } }

  &__img {
    position: relative;
    img {
      display: block;
      width: 75%;
      height: auto;
      margin: 0 auto; } }

  .nav {
    margin-bottom: 32px;
    .nav-item {
      background: #F7F7F7;
      min-width: 33.333333%;
      text-align: center;
      &:first-child {
        border-radius: 5px 5px 0 0; }
      &:last-child {
        border-radius: 0 0 5px 5px; }
      a {
        font-size: 10px;
        line-height: 40px;
        font-weight: 700;
        display: block;
        border-radius: 5px;
        color: #CACACA;
        &.active {
          color: #FFFFFF;
          background: transparent linear-gradient(180deg, #6BBB71 0%, #017961 100%) 0% 0% no-repeat padding-box;
          box-shadow: 0px 3px 6px #00000029; } } } }

  .form-group {
    margin-bottom: 4px; }
  .form-action {
    text-align: right;
    padding-top: 8px;
    p {
      text-align: left; }
    .btn {
      padding-left: 40px;
      padding-right: 40px; } }

  @include mobile {
    padding-bottom: 54px;
    &__close {
      top: 40px; }
    &__wrap {
      padding: 40px 0; }
    &__box {
      .login__slide {
        padding: 30px 26px 80px;
        display: block;
        img {
          width: auto; }
        .owl-dots {
          bottom: 26px; }
        .owl-dot span {
          background: #CBCBCB; }
        .owl-dot.active span {
          background: #23AE74; } } }
    &__lside,
    &__rside {
      width: 100%;
      max-width: 100%;
      flex: 0 0 100%; }
    &__lside {
      padding-right: 0; }
    &__rside {
      padding-left: 0;
      .login__logo {
        display: block;
        margin-bottom: 40px; }
      &:before {
        display: none; } }
    &__img {
      margin: 61px 0; }
    .nav {
      margin-bottom: 60px; }
    .form-action {
      padding-top: 60px;
      .btn {
        width: 100%; } }
    &__bot {
      padding: 0 0 40px;
      &__left {
        justify-content: center; } } } }


